import React, { Component } from "react";
import { connect } from "react-redux";
import { compose } from "redux";
import { Link } from "react-router-dom";

import notification from "../../../../components/notification";
import { Empty, Input, Tooltip, Collapse, Select, Progress } from "antd";

import Popconfirms from "../../../../components/feedback/popconfirm";
import LayoutWrapper from "../../../../components/utility/layoutWrapper";
import PageHeader from "../../../../components/utility/pageHeader";
import IntlMessages from "../../../../components/utility/intlMessages";
import Scrollbars from "../../../../components/utility/customScrollBar";
import Button from "../../../../components/uielements/button";
import actions from "../../../../redux/companyMaster/project/actions";
import TableWrapper from "../../../Tables/antTables/antTable.style";
import CardWrapper, {
  Box,
  StatusTag,
  customCollapseStyle
} from "../../../App/app.style";
import moment from "moment";
import getLanguage from "../../../App/MyLanguange"

const Panel = Collapse.Panel;
const Option = Select.Option;
const Search = Input.Search;

const redirectTo = "/dashboard/setup/menus";

class Project extends Component {
  state = {
    search: null,
    filter: {
      id_project_parent: null,
      id_customer: null,
      id_employee: null,
      pagination: null
    }
  };

  // Get All data diawal
  componentDidMount() {
    const { getProjects } = this.props;
    getProjects();
  }

  componentDidUpdate() {
    // Display error notif
    if (this.props.project.error) {
      notification("error", this.props.project.error);
      this.props.clearError();
    }

    // Display success notif
    if (this.props.project.success) {
      notification("success", this.props.project.success);
      this.props.clearSuccess();
    }
  }

  // Handle search
  handleSearch = value => {
    const { getProjects } = this.props;
    this.setState({
      filter: {
        id_project_parent: null,
        id_customer: null,
        id_employee: null,
        pagination: null
      }
    });
    getProjects(1, value);
  };

  //handle Search Value
  handleSearchValue = e => {
    const value = e.target.value;

    this.setState({ search: value });
  };

  // Handle Reset search
  handleResetSearch = () => {
    const { getProjects } = this.props;
    this.setState({
      filter: {
        id_project_parent: null,
        id_customer: null,
        id_employee: null,
        pagination: null
      }
    });
    this.setState({ search: null });
    getProjects();
  };

  // Handle filter change
  handleFilterChange = (name, value) => {
    this.setState({
      filter: {
        ...this.state.filter,
        [name]: value
      }
    });
  };

  // Handle filter
  handleFilter = () => {
    const { getProjects } = this.props;
    //const { current_page } = this.props.project;
    const { filter } = this.state;
    let string = "";

    if (filter.id_project_parent) {
      string += "id_project_parent=" + filter.id_project_parent;
    }
    if (filter.id_customer) {
      string
        ? (string += "&id_customer=" + filter.id_customer)
        : (string += "id_customer=" + filter.id_customer);
    }
    if (filter.id_employee) {
      string
        ? (string += "&id_employee=" + filter.id_employee)
        : (string += "id_employee=" + filter.id_employee);
    }
    if (filter.pagination) {
      string
        ? (string += "&pagination=" + filter.pagination)
        : (string += "pagination=" + filter.pagination);
    }
    //console.log(string)
    this.setState({ search: null });
    getProjects(1, null, string);
  };

  // Handle Reset Filter
  handleResetFilter = () => {
    const { getProjects } = this.props;
    this.setState({
      filter: {
        id_project_parent: null,
        id_customer: null,
        id_employee: null,
        pagination: null
      }
    });
    this.setState({ search: null });
    getProjects();
  };

  // Handle action di table
  handleRecord = (action, id) => {
    if (action === "activate") {
      this.props.activateProject(id);
    } else {
      this.props.deactivateProject(id);
    }
  };

  // Handle pagination
  handlePageChange = page => {
    const { getProjects } = this.props;
    const { search } = this.props.project;
    // const { filter } = this.props.project;
    getProjects(page, search);
  };

  listAction = {
    title: "",
    dataIndex: "id_project",
    rowKey: "id_project",
    // width: 100,
    align: "center",
    fixed: "right",
    render: (text, row) => (
      <div>
        <Tooltip
          placement="topLeft"
          title={<IntlMessages id="tooltip.update" />}
        >
          <Link to={`${this.props.match.path}/update/${text}`}>
            <Button size="small" className="invoiceEdtBtn">
              <i className="ion-android-create" />
            </Button>
          </Link>
        </Tooltip>

        {row.is_active ? (
          <Popconfirms
            title={<IntlMessages id="projects.popconfirm.deactivate" />}
            onConfirm={this.handleRecord.bind(this, "deactivate", text)}
            okText={getLanguage({ id: "yes" })}
            cancelText={getLanguage({ id: "no" })}
          >
            <Tooltip
              placement="topRight"
              title={<IntlMessages id="tooltip.deactivate" />}
            >
              <Button size="small" className="invoiceDltBtn">
                <i className="ion-close-circled" />
              </Button>
            </Tooltip>
          </Popconfirms>
        ) : (
            <Popconfirms
              title={<IntlMessages id="projects.popconfirm.activate" />}
              onConfirm={this.handleRecord.bind(this, "activate", text)}
              okText={getLanguage({ id: "yes" })}
              cancelText={getLanguage({ id: "no" })}
            >
              <Tooltip
                placement="topRight"
                title={<IntlMessages id="tooltip.activate" />}
              >
                <Button size="small" className="invoiceRstBtn">
                  <i className="ion-refresh" />
                </Button>
              </Tooltip>
            </Popconfirms>
          )}
      </div>
    )
  };

  render() {
    const columns = [
      {
        title: getLanguage({ id: "code" }),
        dataIndex: "project_code",
        rowKey: "project_code",
        render: text => <span>{text}</span>,
        width: "11%",
      },
      {
        title: getLanguage({ id: "name" }),
        dataIndex: "project_name",
        rowKey: "project_name",
        render: text => <span>{text}</span>,
        width: "11%",
      },
      {
        title: getLanguage({ id: "projects.parent" }),
        dataIndex: "parent",
        rowKey: "parent",
        render: text => {
          if (text !== null) {
            return <span> {text.project_name} </span>;
          } else {
            return <span />;
          }
        },
        width: "11%",
      },
      {
        title: getLanguage({ id: "projects.manager" }),
        dataIndex: "employee_manager",
        rowKey: "employee_manager",
        render: text => {
          if (text !== null && text.last_name !== null) {
            return <span> {text.first_name + " " + text.last_name} </span>;
          } else if (text !== null && text.last_name === null) {
            return <span> {text.first_name} </span>;
          } else {
            return <span />;
          }
        },
        width: "11%",
      },
      {
        title: getLanguage({ id: "customers.pageHeader" }),
        dataIndex: "customer",
        rowKey: "customer",
        render: text => {
          if (text !== null) {
            return <span> {text.customer_name} </span>;
          } else {
            return <span />;
          }
        },
        width: "11%",
      },
      {
        title: getLanguage({ id: "startDate" }),
        dataIndex: "start_date",
        rowKey: "start_date",
        render: text => <span>{moment(text).format("DD/MM/YYYY")}</span>,
        width: "11%",
      },
      {
        title: getLanguage({ id: "endDate" }),
        dataIndex: "end_date",
        rowKey: "end_date",
        render: text => <span>{moment(text).format("DD/MM/YYYY")}</span>,
        width: "11%",
      },
      {
        title: getLanguage({ id: "progress" }),
        dataIndex: "progress",
        rowKey: "progress",
        render: text => (
          <Progress
            percent={text[0].project_history_progress * 100}
            size="small"
          />
        ),
        width: "11%",
      },
      {
        title: getLanguage({ id: "projects.status" }),
        dataIndex: "status",
        rowKey: "status",
        align: "center",
        render: text => {
          // let className;
          let display;
          if (text === 1) {
            display = getLanguage({ id: "new" });
          } else if (text === 2) {
            display = getLanguage({ id: "estimate" });
          } else if (text === 3) {
            display = getLanguage({ id: "approved" });
          } else if (text === 4) {
            display = getLanguage({ id: "started" });
          } else if (text === 5) {
            display = getLanguage({ id: "finished" });
          } else if (text === 6) {
            display = getLanguage({ id: "cancelled" });
          } else if (text === 7) {
            display = getLanguage({ id: "onHold" });
          }
          return <span>{display}</span>;
        },
        width: "11%",
      },
      {
        title: getLanguage({ id: "status" }),
        dataIndex: "is_active",
        rowKey: "is_active",
        align: "center",
        render: text => {
          let className;
          let display;
          if (text === 1) {
            className = "active";
            display = getLanguage({ id: "active" });
          } else {
            className = "nonactive";
            display = getLanguage({ id: "active" });
          }
          return <StatusTag className={className}>{display}</StatusTag>;
        },
        width: "11%"
      }
    ];
    // console.log(this.state.filter);
    // console.log(this.props.project);

    // Data setup
    const { projects } = this.props.project;
    //console.log(this.props.project); //console.log(this.state);
    const dataSource = [];
    const { user } = this.props.auth;
    let { permissions } = user ? user : [];
    permissions = permissions ? permissions : [];

    //console.log(projects);
    if (permissions.includes("Update Project") && columns.length === 10) {
      columns.push(this.listAction);
    }

    Object.keys(projects).map((project, index) => {
      return dataSource.push({
        ...projects[project],
        key: project
      });
    });

    // Pagination Setup
    const { current_page, page_size, total } = this.props.project;

    // console.log(this.props);
    return (
      <LayoutWrapper>
        <Box>
          <PageHeader
            title={<IntlMessages id="projects.pageHeader" />}
            style={{ margin: 0 }}
          >
            <div>
              {permissions.includes("Create Project") ? (
                <Link to={`${this.props.match.path}/add`}>
                  <Button
                    // loading={loading}
                    // disabled={loading}
                    type="primary"
                    className="mateAddInvoiceBtn"
                  >
                    <IntlMessages id="projects.button.add" />
                  </Button>
                </Link>
              ) : null}
              <Button
                onClick={() => this.props.history.replace(redirectTo)}
                style={{ marginLeft: "5px" }}
              >
                <IntlMessages id="button.back" />
              </Button>
            </div>
          </PageHeader>
          <div className="isoInvoiceTableBtn">
            <Search
              placeholder={getLanguage({ id: "code" }) + ", " + getLanguage({ id: "name" })}
              onSearch={this.handleSearch}
              style={{ marginRight: "1%" }}
              value={this.state.search}
              onChange={this.handleSearchValue}
            />
            <div
              style={{
                // width: "20%",
                marginTop: "auto",
                marginRight: "1%"
              }}
            >
              <Button
                type="primary"
                onClick={this.handleSearch.bind(this, this.state.search)}
              >
                <IntlMessages id="button.search" />
              </Button>
            </div>

            <div
              style={{
                width: "30%",
                marginTop: "auto",
                marginRight: "27%"
                // marginLeft: "-2%"
              }}
            >
              <Button onClick={this.handleResetSearch}>
                <IntlMessages id="filter.reset" />
              </Button>
            </div>
          </div>

          <div className="isoFilter">
            <Collapse style={customCollapseStyle}>
              <Panel header={<IntlMessages id="filter.header" />} key="1">
                <div className="isoFilterContent">
                  <div style={{ width: "50%" }}>
                    <label>
                      <IntlMessages id="projects.parent" />
                    </label>
                    <br />
                    <Select
                      showSearch
                      onChange={this.handleFilterChange.bind(
                        this,
                        "id_project_parent"
                      )}
                      value={
                        this.state.filter.id_project_parent
                          ? this.state.filter.id_project_parent
                          : []
                      }
                      style={{ width: "95%" }}
                      placeholder={<IntlMessages id="projects.select.parent" />}
                      optionFilterProp="children"
                      filterOption={true}
                      loading={this.props.project.loading}
                      allowClear={true}
                    //showArrow={false}
                    >
                      {this.props.project.project_parents.map((data, key) => (
                        <Option key={key} value={data.id_project}>
                          {data.project_code + " (" + data.project_name + ")"}
                        </Option>
                      ))}
                    </Select>
                  </div>

                  <div style={{ width: "50%" }}>
                    <label>
                      <IntlMessages id="projects.manager" />
                    </label>
                    <br />
                    <Select
                      showSearch
                      onChange={this.handleFilterChange.bind(
                        this,
                        "id_employee"
                      )}
                      value={
                        this.state.filter.id_employee
                          ? this.state.filter.id_employee
                          : []
                      }
                      style={{ width: "100%" }}
                      placeholder={
                        <IntlMessages id="projects.select.manager" />
                      }
                      optionFilterProp="children"
                      filterOption={true}
                      loading={this.props.project.loading}
                      allowClear={true}
                    //showArrow={false}
                    >
                      {this.props.project.employees.map((data, key) => (
                        <Option key={key} value={data.id_employee}>
                          {data.employee_code +
                            " (" +
                            data.first_name +
                            (data.last_name ? " " + data.last_name : "") +
                            ")"}
                        </Option>
                      ))}
                    </Select>
                  </div>
                </div>
                <div
                  className="isoFilterContent"
                  style={{ paddingTop: "10px" }}
                >
                  <div style={{ width: "50%" }}>
                    <label>
                      <IntlMessages id="projects.customer" />
                    </label>
                    <br />
                    <Select
                      showSearch
                      onChange={this.handleFilterChange.bind(
                        this,
                        "id_customer"
                      )}
                      value={
                        this.state.filter.id_customer
                          ? this.state.filter.id_customer
                          : []
                      }
                      style={{ width: "95%" }}
                      placeholder={
                        <IntlMessages id="projects.select.customer" />
                      }
                      optionFilterProp="children"
                      filterOption={true}
                      loading={this.props.project.loading}
                      allowClear={true}
                    //showArrow={false}
                    >
                      {this.props.project.customers.map((data, key) => (
                        <Option key={key} value={data.id_customer}>
                          {data.customer_code + " (" + data.customer_name + ")"}
                        </Option>
                      ))}
                    </Select>
                  </div>

                  <div style={{ width: "15%" }}>
                    <label>
                      <IntlMessages id="dataPerPage" />
                    </label>
                    <br />
                    <Select
                      showSearch
                      onChange={this.handleFilterChange.bind(
                        this,
                        "pagination"
                      )}
                      value={
                        this.state.filter.pagination
                          ? this.state.filter.pagination
                          : page_size
                      }
                      style={{ width: "90%" }}
                      optionFilterProp="children"
                      filterOption={true}
                      loading={this.props.project.loading}
                    //allowClear={true}
                    //showArrow={false}
                    >
                      <Option key={1} value={10}>
                        10
                      </Option>
                      <Option key={2} value={25}>
                        25
                      </Option>
                      <Option key={3} value={50}>
                        50
                      </Option>
                      <Option key={4} value={100}>
                        100
                      </Option>
                    </Select>
                  </div>

                  <div
                    style={{
                      width: "33%",
                      marginTop: "auto"
                    }}
                  >
                    <Button
                      size="small"
                      type="primary"
                      style={{ marginRight: "10px" }}
                      onClick={this.handleFilter}
                    >
                      <IntlMessages id="filter.submit" />
                    </Button>
                    <Button onClick={this.handleResetFilter} size="small">
                      <IntlMessages id="filter.reset" />
                    </Button>
                  </div>
                </div>
              </Panel>
            </Collapse>
          </div>

          <CardWrapper>
            {projects.length === 0 && !this.props.project.loading ? (
              <Empty
                description={<IntlMessages id="projects.table.noData" />}
              />
            ) : (
                <div className="isoInvoiceTable">
                  <Scrollbars style={{ width: "100%" }}>
                    <TableWrapper
                      dataSource={dataSource}
                      columns={columns}
                      loading={this.props.project.loading}
                      className="invoiceListTable"
                      bordered={true}
                      // Set pagination, jika ada page_size mengunakan pagination API
                      // jika tidak ada page_size menggunakan pagination bawaan
                      pagination={
                        page_size
                          ? {
                            current: current_page,
                            pageSize: parseInt(page_size),
                            total: total,
                            onChange: this.handlePageChange.bind(this)
                          }
                          : true
                      }
                    />
                  </Scrollbars>
                </div>
              )}
          </CardWrapper>
        </Box>
      </LayoutWrapper>
    );
  }
}

const mapStateToProps = state => {
  return {
    project: state.project,
    auth: state.auth
  };
};

export default compose(
  connect(
    mapStateToProps,
    actions
  )
)(Project);
